import {Button, FormControl, TextareaAutosize, TextField} from "@mui/material";
import {useState} from "react";

export default function ContactForm() {
    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [message, setMessage] = useState("");
    const setter = (varSetter) => {
        return (event) => varSetter(event.target.value);
    }
    const check = () => {
        if (name === "" || email === "" || message === "") return false;
        console.log("email sent");
        setName("");
        setEmail("");
        setMessage("");
    }
    return (
        <FormControl style={{width: "100%"}}>
            <TextField id="Name-Field" label="Name" variant="standard" value={name} onChange={setter(setName)}/>
            <br/>
            <TextField id="Email-Field" label="Email" variant="standard" value={email} onChange={setter(setEmail)}/>
            <br/>
            <TextField id="Message-Field" label="Message" variant="standard" value = {message} multiline onChange={setter(setMessage)}/>
            <br/>
            <Button variant="text" onClick={check}>Send Me a Message!</Button>
        </FormControl>
    )
}