import Navbar from "./Navbar";
import Button from '@mui/material/Button';
import HomeCard from "./HomeCard";
import BlogPosts from "./BlogPosts";
import ProjectView from "./ProjectView";
import './App.css';
import {useState} from "react";
import Grid from "@mui/material/Grid";
import ContactForm from "./ContactForm";

const pages = ['Home', 'Blog', 'Projects', 'Contact']

function App() {
    const [currPage, setCurrentPage] = useState("Home");
    const changeCurrentPage = function(pageId) {
        if (pages.includes(pageId)) {
            setCurrentPage(pageId);
        }
    }
  return (
    <div style={{height: "100%"}}>
      <header className="App-header">
        <Navbar pages={pages} pageChange = {changeCurrentPage} currPage={currPage}/>
      </header>
        <Grid
            container
            spacing={0}
            direction="column"
            alignItems="center"
            justifyContent="center"
            style={{ minHeight: '100vh', paddingTop: "5em"}}
        >

            <Grid style={{width: "80%", textAlign: "center"}}>
                {currPage === "Home" && <HomeCard/>}
                {currPage === "Blog" && <BlogPosts />}
                {currPage === "Projects" && <ProjectView/>}
                {currPage === "Contact" && <ContactForm/>}
            </Grid>

        </Grid>
    </div>
  );
}

export default App;
